import Vue from 'vue'
import Router from 'vue-router'

import Map from '@/components/Map.vue'
import Confirm from '@/components/Confirm.vue'
import DestinationSearch from '@/components/DestinationSearch.vue'
import DriveDestination from '@/components/DriveDestination.vue'
import DriveOrigin from '@/components/DriveOrigin.vue'
import Rating from '@/components/Rating.vue'
import SessionLogin from '@/components/SessionLogin.vue'

Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [

        {
            path: '/',
            name: 'SessionLogin',
            component: SessionLogin,
            meta: {
                notRequiresAuth: true
            }
        },
        {
            path: '/map',
            name: 'Map',
            component: Map
        },
        
        {
            path: '/destinationSearch',
            name: 'DestinationSearch',
            component: DestinationSearch
        },

        {
            path: '/confirm',
            name: 'Confirm',
            component: Confirm
        },

        {
            path: '/driveOrigin',
            name: 'DriveOrigin',
            component: DriveOrigin
        },

        {
            path: '/driveDestination',
            name: 'DriveDestination',
            component: DriveDestination,
        },

        {
            path: '/rating',
            name: 'Rating',
            component: Rating,
        },
    ]
})
