import Vue from "vue";
import App from "./App.vue";
import store from "./store/store";
import router from "./router/router";
import * as VueGoogleMaps from "vue2-google-maps";
import { IconsPlugin } from 'bootstrap-vue'

Vue.config.productionTip = false;
Vue.use(IconsPlugin);
Vue.use(VueGoogleMaps, {
    load: {
        key: "AIzaSyAixRzVqb7kii3XrzzL3OxsN7k_2CoMeaM",
        libraries: "places, directions, geolocation, geometry",
    },
});

document.addEventListener('visibilitychange', () => {
    if (document.visibilityState === 'hidden') {
        if (store.state.sessionId) {
            store.state.mqttBridge.disconnect();
            store.dispatch('resetStateToDefault');
        }
    }
});

router.beforeEach((to, _from, next) => {
    // Check if the route requires authentication
    if (to.matched.some(record => !record.meta.notRequiresAuth)) {
        // Check if the user is authenticated
        if (!store.state.isSessionValid) {
            // Redirect to the login page if not authenticated
            next({ path: '/' });
        } else {
            const sessionId = store.state.sessionId;
            // Only modify the route if sessionId is missing (will happen once after logging in)
            if (to.query.sessionId !== sessionId) {
                next({
                    ...to,
                    query: {
                        ...to.query,
                        sessionId: sessionId,
                    },
                });
            } else {
                next(); // Proceed if sessionId is already correct
            }
        }
    } else {
        // If the route does not require authentication:

        // Check if sessionId exists in the query params
        if ('sessionId' in to.query) {
            // Remove sessionId from query params
            next({
                ...to,
                query: Object.fromEntries(
                    Object.entries(to.query).filter(([key]) => key !== 'sessionId')
                ),
            });
        } else {
            // Proceed without modifying the route if sessionId is not present
            next();
        }
    }
});

// Suppress navigation errors caused by redirections
const originalPush = router.push;
router.push = function push(location, onResolve, onReject) {
    if (onResolve || onReject) {
        return originalPush.call(this, location, onResolve, onReject);
    }
    return originalPush.call(this, location).catch((err) => {
        if (!/redirected/i.test(err.message)) {
            throw err; // Re-throw unexpected errors
        }
    });
};
const originalReplace = router.replace;
router.replace = function replace(location, onResolve, onReject) {
    if (onResolve || onReject) {
        return originalReplace.call(this, location, onResolve, onReject);
    }
    return originalReplace.call(this, location).catch((err) => {
        if (!/redirected/i.test(err.message)) {
            throw err; // Re-throw unexpected errors
        }
    });
};

new Vue({
    store,
    router,
    render: (h) => h(App),
}).$mount("#app");
