<template>
    <div class="my-container">
        <div class="box">
            <h3 class="textClass">{{$store.state.currentLang.rating}}</h3>
            <star-rating inactive-color="white" v-model="rating" v-bind:show-rating=false @rating-selected="setRating"></star-rating>
        </div>
    </div>
</template>

<script>
import StarRating from 'vue-star-rating';
import { mapGetters } from 'vuex';

export default {
    components: {
        StarRating,
    },
    data() {
        return {
            rating: null
        }
    },

    computed: {
        ...mapGetters(['getIP', 'getMqttBridge'])
    },

    methods: {
        setRating() {
            const current = new Date();
            const date = current.getFullYear()+'-'+(current.getMonth()+1)+'-'+current.getDate();
            const time = current.getHours() + ":" + current.getMinutes() + ":" + current.getSeconds();
            const dateTime = date +' '+ time;
            this.getMqttBridge.publishRating(
                {
                    datetime: dateTime,
                    rating: this.rating,                    
                }
            );
            this.$router.push('/map');
        }
    },
    
}
</script>

