import { MapElementFactory } from "vue2-google-maps";

export default MapElementFactory({
    name: "directionsRenderer",
    ctr() {
        return window.google.maps.DirectionsRenderer;
    },
    events: [],
    mappedProps: {},
    props: {
        origin: { type: [Object, Array] },
        destination: { type: [Object, Array] },
        waypoints: { type: Array, default: () => [] }, // Array of intermediate points
        travelMode: { type: String },
        suppressMarkers: { type: Boolean },
        onDirectionsSet: { type: Function },  // Callback for after directions are set
    },
    afterCreate(directionsRenderer) {
        let directionsService = new window.google.maps.DirectionsService();
        this.$watch(
            () => [
                this.origin,
                this.destination,
                this.waypoints,
                this.travelMode,
                this.suppressMarkers,
            ],
            () => {
                let { origin, destination, waypoints, travelMode, suppressMarkers } = this;
                if (!origin || !destination || !travelMode) return;
                directionsService.route(
                    {
                        origin,
                        destination,
                        waypoints: waypoints.map(point => ({ location: point, stopover: true })), // Add waypoints
                        travelMode,
                    },
                    (response, status) => {
                        if (status !== "OK" || !response || !response.routes || !response.routes[0]) return;
                        if (suppressMarkers === true) 
                            directionsRenderer.setOptions({
                                suppressMarkers: true,
                                preserveViewport: true,
                            });
                        directionsRenderer.setDirections(response);

                        // Get the bounds directly from the route
                        const bounds = response.routes[0].bounds;

                        // Calculate the zoom level based on the bounds of the path
                        const map = directionsRenderer.getMap();
                        if (!map) return; // Ensure the map is available
                        map.fitBounds(bounds);  // Automatically centers the map
                    }
                );
            },
            { immediate: true }
        );
    },
});
