import { getDistance } from 'geolib';
import stopsData from '@/assets/stops.json'; // Import stops data from JSON file

export class StopFinder {
  constructor() {
    this.stops = stopsData.stops; // Load stops from the JSON file
  }

  findClosestStops(point, maxDistance = 0.5, maxStops = 3) {
    // Find stops within maxDistance of the point
    const stopsWithinDistance = this.stops
      .map((stop) => {
        const distance = getDistance(
          { latitude: point[0], longitude: point[1] },
          { latitude: stop.position.lat, longitude: stop.position.lng }
        ) / 1000; // Convert meters to kilometers
        return {
          name: stop.name,
          position: stop.position,
          dist_from_point: distance,
        };
      })
      .filter((stop) => stop.dist_from_point <= maxDistance);

    // Sort stops by distance from the point
    const sortedStops = stopsWithinDistance.sort(
      (a, b) => a.dist_from_point - b.dist_from_point
    );
    // Return the first maxStops
    return sortedStops.slice(0, maxStops);
  }
}