<template>
    <div class="login-container">
        <img v-if="getLang === 'en'" @click="changeLang" class="languageButton" src="../assets/estonia.png" />
        <img v-if="getLang === 'est'" @click="changeLang" class="languageButton" src="../assets/united-kingdom.png" />
        <h1>{{ $store.state.currentLang.enterSessionId }}</h1>
        <div class="code-input-container">
            <input
                v-for="(digit, index) in sessionIdArray"
                :key="index"
                v-model="sessionIdArray[index]"
                type="text"
                inputmode="numeric"
                pattern="[0-9]*"
                class="code-input"
                @input="onInput(digit, index, $event)"
                @keydown.backspace="onBackspace(index, $event)"
                @keydown.arrow-left="onArrowLeft(index)"
                @keydown.arrow-right="onArrowRight(index)"
                @keydown.enter="validateSession"
                @paste="onPaste(index, $event)"
                ref="codeInput"
                :disabled="validationStatus"
            />
        </div>
        <button @click="validateSession" class="validate-button" :disabled="validationStatus">
            {{ $store.state.currentLang.validate }}
        </button>
        <div class="info-message-container">
            <p v-if="validationStatus" class="status">{{ validationStatus }}</p>
            <p v-if="errorMessage" class="error">{{ errorMessage }}</p>
            <div v-if="validationStatus" class="loader"></div> 
        </div>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import { MqttBridge } from '@/utils/mqttBridge';

export default {
    data() {
        return {
            sessionIdArray: ["", "", "", "", "", ""], // Array for 6-digit code
            errorMessage: "",
            currentError: null,
        };
    },
    computed: {
        ...mapGetters(['getLang', 'getCurrentLang']),
        validationStatus() {
            return this.$store.state.sessionValidationStatus;
        },
        sessionId() {
            return this.sessionIdArray.join("");
        },
    },
    methods: {
        ...mapActions(['setLang', 'setCurrentLang', 'setMqttBridge']),
        changeLang: function() {
            if (this.getLang === 'est') {
                this.setLang('en');
                this.setCurrentLang(this.$store.state.locale.en);
            }
            else {
                this.setLang('est');
                this.setCurrentLang(this.$store.state.locale.est);
            }
            
            if (this.currentError) {
                switch (this.currentError) {
                    case "incorrectFormatError":
                        this.errorMessage = this.$store.state.currentLang.incorrectFormatError;
                        break;
                    case "sessionNotFoundError":
                        this.errorMessage = this.$store.state.currentLang.sessionNotFoundError;
                        break;
                    default:
                        this.errorMessage = this.$store.state.currentLang.sessionNotFoundError;
                }
            }
            if (this.validationStatus) {
                this.$store.dispatch(
                    "setSessionValidationStatus",
                    this.$store.state.currentLang.attemptingToConnect
                );            
            }
        },
        onInput(digit, index, event) {
            let value = event.target.value.replace(/\D/g, ""); // Allow only digits
            
            if (value.length > 1) {
                value = value.replace(digit, ""); // Remove the current value from the input
            }

            if (value.length > 0) {
                // Overwrite the current cell with the first digit
                this.$set(this.sessionIdArray, index, value[0]);

                // Automatically move to the next input if a digit is entered
                if (index < this.sessionIdArray.length - 1) {
                    this.$refs.codeInput[index + 1].focus();
                }
            } else {
                // Clear the current cell if no value is entered
                this.$set(this.sessionIdArray, index, "");
            }

            // Ensure the input field visually reflects the single digit
            event.target.value = this.sessionIdArray[index];
        },
        onBackspace(index, event) {
            // Move to the previous input if backspace is pressed
            event.preventDefault(); // Prevent default backspace behavior
            if (index > 0) {
                this.$set(this.sessionIdArray, index, "");
                this.$refs.codeInput[index - 1].focus();
            } else {
                this.$set(this.sessionIdArray, index, "");
            }
        },
        onArrowLeft(index) {
            // Move to the previous input if left arrow is pressed
            if (index > 0) {
                this.$refs.codeInput[index - 1].focus();
            }
        },
        onArrowRight(index) {
            // Move to the next input if right arrow is pressed
            if (index < this.sessionIdArray.length - 1) {
                this.$refs.codeInput[index + 1].focus();
            }
        },
        onPaste(index, event) {
            event.preventDefault(); // Prevent default paste behavior

            const pastedData = (event.clipboardData || window.clipboardData).getData('text'); // Get the pasted text
            if (!/^\d+$/.test(pastedData)) {
                // If the pasted text contains non-digit characters, ignore it
                return;
            }

            // Distribute the digits across the input fields starting from the current index
            let currentIndex = index;
            for (let char of pastedData) {
                if (currentIndex >= this.sessionIdArray.length) {
                    break; // Stop if we run out of input fields
                }
                this.$set(this.sessionIdArray, currentIndex, char); // Set the digit in the current cell
                currentIndex++;
            }

            // Move focus to the next empty cell or the last cell filled
            if (currentIndex < this.sessionIdArray.length) {
                this.$refs.codeInput[currentIndex].focus();
            } else {
                this.$refs.codeInput[this.sessionIdArray.length - 1].focus();
            }
        },
        async validateSession() {
            if (!this.sessionId || this.sessionId.length !== 6 || isNaN(this.sessionId)) {
                this.errorMessage = this.$store.state.currentLang.incorrectFormatError;
                this.currentError = "incorrectFormatError";
                return;
            }

            this.$store.dispatch(
                "setSessionValidationStatus",
                this.$store.state.currentLang.attemptingToConnect
            );
            this.errorMessage = "";
            this.currentError = null;

            const mqttBridge = new MqttBridge(this.sessionId);
            mqttBridge.connect();
            
            const isValid = await mqttBridge.validateSession();

            if (isValid) {
                this.setMqttBridge(mqttBridge);

                await this.$store.dispatch("setIsSessionValid", true);
                await this.$store.dispatch("setSessionValidationStatus", null);
                await this.$store.dispatch("setSessionId", this.sessionId);
                
                const urlParams = new URLSearchParams(window.location.search);
                urlParams.set("session_id", this.sessionId);
                const newUrl = `${window.location.origin}${window.location.pathname}?${urlParams.toString()}${window.location.hash}`;
                window.history.replaceState({}, '', newUrl);

                this.$router.push("/map"); // Redirect to the main map page
            } else {
                this.errorMessage = this.$store.state.currentLang.sessionNotFoundError;
                this.currentError = "sessionNotFoundError";
                this.$store.dispatch("setSessionValidationStatus", null);
            }
        },
    },
    mounted() {
        const urlParams = new URLSearchParams(window.location.search);
        const sessionId = urlParams.get("session_id");

        if (sessionId && sessionId.length === 6 && !isNaN(sessionId)) {
            // Populate the sessionIdArray
            this.sessionIdArray = sessionId.split("");

            // Call validateSession automatically
            this.validateSession();
        } else {
            // Focus on the first input field when the component is mounted
            this.$refs.codeInput[0].focus();
        }
    },
};
</script>

