    }
}

.code-input:focus {
    border-color: #007bff;
    box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
    background-color: #c4e5f5;
}
.validate-button {
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
}
.validate-button:hover {
    background-color: #0056b3;
}
.validate-button:disabled {
    background-color: #686868;
    cursor: not-allowed; 
    opacity: 0.6; 
}
.info-message-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 80%;
    margin-top: 20px;
    font-size: 18px;
}
.status {
    color: blue;
    margin-top: 10px;
}
.error {
    color: red;
    margin-top: 10px;
}
.loader {
  border: 16px solid #adadad; /* Light grey */
  border-top: 16px solid #2987c5; /* Blue */
  border-radius: 50%;
  width: 60px;
  height: 60px;
  animation: spin 2s linear infinite;
  margin-top: 10px;
}
@keyframes spin {
  0% { transform: rotate(0deg); }
