import mqtt from 'mqtt';

export class MqttBridge {
  constructor(sessionId, isDev = false) {
    this.sessionId = sessionId;
    this.isDev = isDev;
    this.client = null;
    this.pose = null;
    this.speed = 0.0;
    this.waypoints = null;
    this.isSessionValid = false;

    this.topics = {
      published: {
        GOAL: `session/${sessionId}/goal`,
        RATING: `session/${sessionId}/rating`,
      },
      received: {
        STATUS: `session/${sessionId}/status`,
        ROUTE: `session/${sessionId}/route`,
      },
    };
  }

  connect(host = "mqtt.cloud.ut.ee", port = 8084, useTLS = true) {
    const protocol = useTLS ? 'wss' : 'ws';
    const options  = {
        // Validate server certificate using system CA store
        rejectUnauthorized: true,
        clientId: this.sessionId,
    };
    this.client = mqtt.connect(`${protocol}://${host}:${port}`, options);

    this.client.on('connect', (connack) => {
      if (connack.returnCode === 0) {
        if (this.isDev)
          console.log(`Connected to MQTT host with session ID: ${this.sessionId}`);
        this.client.subscribe(this.topics.received.STATUS);
        this.client.subscribe(this.topics.received.ROUTE, { qos: 1 });
      } else {
        console.error(
          `Failed to connect to MQTT host. Reason: ${mqtt.CONNACK.returnCode[connack.returnCode]}`
        );
      }
    });

    this.client.on('message', (topic, message) => {
      const data = JSON.parse(message.toString());
      if (topic === this.topics.received.STATUS) {
        this.pose = data.pose;
        this.speed = data.speed;
        this.isSessionValid = true;
      } else if (topic === this.topics.received.ROUTE) {
        this.waypoints = data.waypoints;
      }
    });
  }

  validateSession(timeout = 3000) {
    return new Promise((resolve) => {
      this.isSessionValid = false; // Reset session validity
      const startTime = Date.now();

      const interval = setInterval(() => {
        if (this.isSessionValid) {
          clearInterval(interval);
          resolve(true); // Session is valid
        } else if (Date.now() - startTime > timeout) {
          clearInterval(interval);
          this.disconnect(); // Disconnect if session is invalid
          resolve(false); // Session is invalid
        }
      }, 100); // Check every 100ms
    });
  }

  disconnect() {
    this.client.end();
    if (this.isDev) 
      console.log('Disconnected from MQTT host:', this.sessionId);
  }

  publishGoal(data) {
    this.client.publish(this.topics.published.GOAL, JSON.stringify(data), { qos: 1 });
  }

  publishRating(data) {
    this.client.publish(this.topics.published.RATING, JSON.stringify(data), { qos: 1 });
  }

  getPose() {
    return this.pose;
  }

  getSpeed() {
    return this.speed;
  }

  getWaypoints() {
    return this.waypoints;
  }

  nullWaypoints() {
    this.waypoints = null;
  }
}