<template>
    <div class="my-container">
        <div class="box" ref="box">
            <!-- If no stops are found, show specific error messages -->
            <div class="noStopsContainer" v-if="noStopsFound">
                <h3 class="textClass">
                    <template v-if="getOrigin.error && getDestination.error">
                        {{$store.state.currentLang.noStopsErrorBoth}}
                    </template>
                    <template v-else-if="getOrigin.error">
                        {{$store.state.currentLang.noStopsErrorPickup}}
                    </template>
                    <template v-else-if="getDestination.error">
                        {{$store.state.currentLang.noStopsErrorDropoff}}
                    </template>
                </h3>
                <button @click="backClicked" class="button red" tag="button"> {{$store.state.currentLang.back}} </button>
            </div>

            <!-- If stops are found, show dropdowns for pickup and dropoff -->
            <div v-else-if="selectedPickup && selectedDropoff" class="dropdown-container">
                <div class="dropdowns">
                    <h3 class="textClass dropdownInfo">{{$store.state.currentLang.choosePickup}}</h3>
                    <select v-model="selectedPickup" class="dropdown">
                        <option v-for="(stop, index) in pickupOptions" :key="index" :value="stop">
                            {{ stop.name }}
                        </option>
                    </select>

                    <h3 class="textClass dropdownInfo">{{$store.state.currentLang.chooseDropoff}}</h3>
                    <select v-model="selectedDropoff" class="dropdown">
                        <option v-for="(stop, index) in dropoffOptions" :key="index" :value="stop">
                            {{ stop.name }}
                        </option>
                    </select>
                </div>
                
                <button class="button green" @click="orderCartoUser">{{$store.state.currentLang.orderCar}}</button>
                <button @click="cancelClicked" class="button red" tag="button">{{$store.state.currentLang.cancel}}</button>
            </div>
        </div>
        <GmapMap 
            :map-type-id='mapType' 
            :center='center' 
            :zoom='zoom' 
            :style="{ width: width + '%', height: height + 'px' }" 
            :options='mapOptions'
        >
            <!-- Markers -->
            <GmapMarker :position="getUserLocation.position" :icon="{ url: require('../assets/bluecircle.png')}" />
            <GmapMarker :position="getCarLocation" :icon="{ url: require('../assets/carmarker.png')}" />
            <div v-if="!noStopsFound && selectedPickup && selectedDropoff">
                <GmapMarker 
                    v-for="(pickup, index) in pickupOptions" 
                    :key="'pickup-' + index" 
                    :position="pickup.position" 
                    :icon="{ 
                        url: require('../assets/origin.svg'), 
                        scaledSize: { width: 40, height: 50 }, 
                        anchor: { x: 20, y: 25 } 
                    }" 
                    :zIndex="1000" 
                    :opacity="pickup === selectedPickup ? 1 : 0.5" 
                    :clickable="pickup !== selectedPickup" 
                    @click="onPickupMarkerClick(pickup)"
                />

                <GmapMarker 
                    v-for="(dropoff, index) in dropoffOptions" 
                    :key="'dropoff-' + index" 
                    :position="dropoff.position" 
                    :icon="{ 
                        url: require('../assets/flag.svg'), 
                        scaledSize: { width: 40, height: 50 }, 
                        anchor: { x: 5, y: 45 } 
                    }" 
                    :zIndex="1000" 
                    :opacity="dropoff === selectedDropoff ? 1 : 0.5" 
                    :clickable="dropoff !== selectedDropoff" 
                    @click="onDropoffMarkerClick(dropoff)"
                />
            </div>
            
            <DirectionsRenderer 
                v-if="!noStopsFound && selectedPickup && selectedDropoff"
                travelMode="DRIVING" 
                :origin="selectedPickup.position" 
                :destination="selectedDropoff.position" 
                :suppressMarkers="true" 
            />
        </GmapMap>
    </div> 
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import DirectionsRenderer from './DirectionsRenderer.js';

export default {
    name: 'Confirm',
    components: {
        DirectionsRenderer
    },
    data() {
        return {
            center: { lat: 58.379717565420194, lng: 26.72276002855532 },
            currentPlace: null,
            zoom: 13,
            mapType: 'roadmap',
            mapOptions: {zoomControl: false, fullscreenControl: false, streetViewControl: false, mapTypeControl: false, preserveViewport: true},
            userLocation: null,
            height: 0,
            width: 100,
            pickupOptions: [], // List of pickup options (primary + secondary stops)
            dropoffOptions: [], // List of dropoff options (primary + secondary stops)
            selectedPickup: null, // Selected pickup stop
            selectedDropoff: null // Selected dropoff stop
        }
    },
    computed: {
        ...mapGetters(['getDestination', 'getOrigin', 'getCarLocation', 'getUserLocation', 'getIP', 'getWaypoints', 'getPickupOptions', 'getDropoffOptions', 'getMqttBridge']),
        // If getWaypoints array exists and is not empty, we consider custom route active.
        hasCustomWaypoints() {
            return this.getWaypoints && this.getWaypoints.length > 0;
        },
        // Check if no stops were found
        noStopsFound() {
            return this.getOrigin.error || this.getDestination.error;
        }
    },
    methods: {
        ...mapActions(['setDestination', 'setOrigin', 'setSentBacktoSelection', 'setDropoffPosition']),
        backClicked() {
            this.setSentBacktoSelection(true);
            this.$router.push("/destinationSearch");
        },
        cancelClicked() {
            this.setSentBacktoSelection(true);
            this.$router.push("/destinationSearch");
        },
        onPickupMarkerClick(pickup) {
            if (pickup !== this.selectedPickup) {
                this.selectedPickup = pickup;
            }
        },
        onDropoffMarkerClick(dropoff) {
            if (dropoff !== this.selectedDropoff) {
                this.selectedDropoff = dropoff;
            }
        },
        orderCartoUser() {
            let publishedData = {
                lat: this.selectedPickup.position.lat,
                lng: this.selectedPickup.position.lng,
                height: this.selectedPickup.position.height
            }
            this.getMqttBridge.publishGoal(publishedData);
            
            
            //Store dropoff in Vuex store
            this.setDropoffPosition(publishedData);

            // Set the selected pickup and dropoff in Vuex store
            this.setOrigin(this.selectedPickup);
            this.setDestination(this.selectedDropoff);

            this.$router.push('/driveOrigin');
        },
    },
    created() {
        this.height = window.innerHeight;
        this.pickupOptions = this.getPickupOptions;
        this.dropoffOptions = this.getDropoffOptions;

        this.selectedPickup = this.pickupOptions[0] || null;
        this.selectedDropoff = this.dropoffOptions[0] || null;
    },
    beforeDestroy() {
        clearInterval(this.intervalId);
    }

}
</script>

