<template>
    <div class="my-container">
        <div class="box" v-if="carHasArrived">
            <h3 class="textClass">{{$store.state.currentLang.arriveDropoff}}</h3>
            <h3 class="textClass">{{$store.state.currentLang.areYouOut}}</h3>
            <button class="button green" @click="endDrive">{{$store.state.currentLang.yes}}</button>
        </div>
        <div class="box" style="height: 60px" v-else>
            <h3 class="textClass">{{$store.state.currentLang.speed}}{{velocity}} km/h</h3>
        </div>
        <GmapMap
            ref="mapRef" 
            :map-type-id='mapType' 
            :center='center' 
            :zoom='zoom' 
            :style="{ width: width + '%', height: height + 'px' }" 
            :options='mapOptions'
        >
            <GmapMarker :position="getCarLocation" :icon="{ url: require('../assets/carmarker.png')}" />
            <GmapMarker :position="getDestination.position" />
            
            <!-- Custom route if waypoints exist -->
            <!-- Completed part (blue) -->
            <GmapPolyline 
                v-if="!carHasArrived && hasCustomWaypoints"
                :path="completedPath"
                :options="{ strokeColor: '#0000FF', strokeOpacity: 1.0, strokeWeight: 4 }"
            />
            <!-- Remaining part (red) -->
            <GmapPolyline 
                v-if="!carHasArrived && hasCustomWaypoints"
                :path="remainingPath"
                :options="{ strokeColor: '#FF0000', strokeOpacity: 1.0, strokeWeight: 4 }"
            />
            <!-- Else fallback to DirectionsRenderer -->
            <DirectionsRenderer 
                v-else-if="!carHasArrived && getCarLocation && getDestination.position"
                travelMode="DRIVING" 
                :origin="getCarLocation" 
                :destination="getDestination.position" 
                :suppressMarkers="true" 
            />
        </GmapMap>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import DirectionsRenderer from './DirectionsRenderer.js';

export default {
    name: 'DriveDestination',
    components: {
        DirectionsRenderer,
    },

    data() {
        return {
            center: { lat: 58.379717565420194, lng: 26.72276002855532 },
            currentPlace: null,
            zoom: 15,
            mapType: 'roadmap',
            mapOptions: {zoomControl: false, fullscreenControl: false, streetViewControl: false, mapTypeControl: false, preserveViewport: true },
            userLocation: null,
            height: 0,
            width: 100,
            pollingDist: null,
            pollingVel: null,
            wpPolling: null,
            eventPolling: null,
            velocity: 0,
            carHasArrived: false,
            mapObject: null,
            pathHolder: null,
            mqttBridge: null,
        }
    },
    computed: {
        ...mapGetters(['getDestination', 'getOrigin', 'getCarLocation', 'getUserLocation', 'getIP', 'getWaypoints', 'getMqttBridge']),
        hasCustomWaypoints() {
            return this.getWaypoints && this.getWaypoints.length > 0;
        },
        polylinePath() {
            return this.hasCustomWaypoints
                ? this.getWaypoints.map(point => ({
                      lat: Number(point[0]),
                      lng: Number(point[1])
                  }))
                : [];
        },
        completedPath() {
            return this.getCompletedPath();
        },
        remainingPath() {
            return this.getRemainingPath();
        }
    },
    watch: {
        polylinePath: {
            immediate: true,
            handler(newPath) {
                if (newPath.length > 0 && this.pathHolder !== newPath) {
                    const newPathString = JSON.stringify(newPath);
                    if (this.pathString !== newPathString) {
                        this.pathString = newPathString;
                        const bounds = new google.maps.LatLngBounds();
                        newPath.forEach(point => {
                            bounds.extend(new google.maps.LatLng(point.lat, point.lng));
                        });

                        // Use $nextTick to ensure the map is ready before fitting bounds
                        this.$nextTick(() => {
                            if (this.$refs.mapRef && this.$refs.mapRef.$mapObject) {
                                this.$refs.mapRef.$mapObject.fitBounds(bounds);
                            }
                        });
                    }
                }
            }
        }
    },
    methods: {
        ...mapActions(['fetchWaypoints', 'nullWaypoints']),
        pollEventState() {
            this.eventPolling = setInterval(() => {
                let wps = this.getWaypoints;
                this.carHasArrived = wps && wps.length === 0;
                if(this.carHasArrived){
                    this.center = this.getCarLocation;
                    this.zoom = 20;
                }
            }, 1000);
        },
        carVelocity() {
            this.pollingVel = setInterval(() => {
                this.velocity = this.mqttBridge.getSpeed();                
            }, 1000);
        },
        endDrive() {
            this.$store.dispatch('setDestination', null);
            this.$store.dispatch('setOrigin', null);
            this.$router.push('/rating');
        },
        getCompletedPath() {
            if (!this.getCarLocation || !this.polylinePath.length) return [];
            let closestIndex = this.findClosestPointIndex();
            return this.polylinePath.slice(0, closestIndex + 1);
        },
        getRemainingPath() {
            if (!this.getCarLocation || !this.polylinePath.length) return [];
            let closestIndex = this.findClosestPointIndex();
            return this.polylinePath.slice(closestIndex);
        },
        findClosestPointIndex() {
            let carPos = this.getCarLocation;
            let minDist = Infinity;
            let closestIndex = 0;

            this.polylinePath.forEach((point, index) => {
                let dist = Math.hypot(carPos.lat - point.lat, carPos.lng - point.lng);
                if (dist < minDist) {
                    minDist = dist;
                    closestIndex = index;
                }
            });

            return closestIndex;
        }
    },
    created() {
        this.nullWaypoints();
        this.mqttBridge = this.getMqttBridge;
        this.height = window.innerHeight - 65;
        this.pollEventState();
        this.carVelocity();
        // Poll for waypoints
        this.fetchWaypoints();
        this.wpPolling = setInterval(() => {
            this.fetchWaypoints();
        }, 1000);
    },
    beforeDestroy() {
        if (this.pollingDist) {
            clearInterval(this.pollingDist);
            this.pollingDist = null;
        }
        if (this.pollingVel) {
            clearInterval(this.pollingVel);
            this.pollingVel = null;
        }
        if (this.wpPolling) {
            clearInterval(this.wpPolling);
            this.wpPolling = null;
        }
        if (this.eventPolling) {
            clearInterval(this.eventPolling);
            this.eventPolling = null;
        }
    },

}
</script>

