<template>
    <div>
        <div class="v1_6">
            <div class="v1_5">{{$store.state.currentLang.enterOrigAndDest}}</div>
            <div class="v1_4">
                <b-icon @click="goBack" icon="x-circle" font-scale="3"></b-icon>
            </div>
        </div>
        <div class="my-container">
            <GmapAutocomplete id="originBox" :placeholder=$store.state.currentLang.enterOrig class="address-box" @focus="showButton" @place_changed='setOriginPlace' :options="autoCompleteOptions" />
            <GmapAutocomplete id="destinationBox" :placeholder=$store.state.currentLang.enterDest class="address-box" @focus="hideButton" @place_changed='setDestinationPlace' :options="autoCompleteOptions" />
            <div class="pac-item" @click="useMyLocation" v-if="showLocationButton">
                <span class="pac-icon pac-icon-marker"></span>
                    <span class="pac-item-query">{{$store.state.currentLang.myLocation}}</span>
            </div>
        </div>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';
export default {
    name: 'DestinationSearch',
    data() {
        return {
            center: { lat: 58.379717565420194, lng: 26.72276002855532 },
            currentPlace: null,
            zoom: 12,
            mapType: 'roadmap',
            mapOptions: {zoomControl: false, fullscreenControl: false, streetViewControl: false, mapTypeControl: false, preserveViewport: true },
            userLocation: null,
            showLocationButton: false,
            autoCompleteOptions: {
                componentRestrictions: {
                    country: ['ee']
                }
            },
            pacobserver: null,
            stopFinder: null,
        }
    },
    
    computed: {
        ...mapGetters(['getMarkers', 'getUserLocation', 'getCarLocation', 'getIP', 'getOrigin', 'getDestination', 'getSentBacktoSelection', 'getStopFinder']),
    },

    methods: {
        ...mapActions(['setDestination', 'setOrigin', 'setSentBacktoSelection', 'setPickupOptions', 'setDropoffOptions']),
        showButton: function() {
            this.showLocationButton = true;
        },

        hideButton: function() {
            this.showLocationButton = false;
        },

        goBack: function() {
            this.$router.push('/map');
        },

        useMyLocation: function() {
            const closestStops = this.stopFinder.findClosestStops([
                this.getUserLocation.position.lat,
                this.getUserLocation.position.lng,
            ]);

            if (closestStops && closestStops.length > 0) {
                this.setOrigin({
                    name: closestStops[0].name,
                    searchedName: this.$store.state.currentLang.myLocation,
                    position: closestStops[0].position,
                });
                this.setPickupOptions(closestStops);
            } else {
                this.setOrigin({
                    position: 'not_found',
                    searchedName: this.$store.state.currentLang.myLocation,
                    error: true,
                });
                this.setPickupOptions([]);
            }
            document.getElementById('originBox').value=this.$store.state.currentLang.myLocation;
            this.showLocationButton = false;
        },

        setOriginPlace(place) {
            const originBox = document.getElementById('originBox');
            originBox.style.border = '#eee'; 

            this.markers = []
            this.currentPlace = place;

            if (!this.currentPlace || !this.currentPlace.geometry || !this.currentPlace.geometry.location) {
                return;
            }

            const marker = {
                lat: this.currentPlace.geometry.location.lat(),
                lng: this.currentPlace.geometry.location.lng(),
            };

            this.markers.push({ position: marker })
            this.currentPlace = null;

            const closestStops = this.stopFinder.findClosestStops([marker.lat, marker.lng]);
            if (closestStops && closestStops.length > 0) {
                this.setOrigin({
                    name: closestStops[0].name,
                    searchedName: originBox.value,
                    position: closestStops[0].position,
                });
                this.setPickupOptions(closestStops);
            } else {
                this.setOrigin({
                    position: 'not_found',
                    searchedName: originBox.value,
                    error: true,
                });
                this.setPickupOptions([]);
            }
        },

        setDestinationPlace(place) {
            const destinationBox = document.getElementById('destinationBox');
            destinationBox.style.border = '#eee'; 

            this.markers = []
            this.currentPlace = place;

            if (!this.currentPlace || !this.currentPlace.geometry || !this.currentPlace.geometry.location) {
                return;
            }

            const marker = {
                lat: this.currentPlace.geometry.location.lat(),
                lng: this.currentPlace.geometry.location.lng(),
            };

            this.markers.push({ position: marker })
            this.currentPlace = null;

            const closestStops = this.stopFinder.findClosestStops([marker.lat, marker.lng]);
            if (closestStops && closestStops.length > 0) {
                this.setDestination({
                    name: closestStops[0].name,
                    searchedName: destinationBox.value,
                    position: closestStops[0].position,
                });
                this.setDropoffOptions(closestStops);
            } else {
                this.setDestination({
                    position: 'not_found',
                    searchedName: destinationBox.value,
                    error: true,
                });
                this.setDropoffOptions([]);
            }
        },
    },
    created: function() {
        if (!this.getSentBacktoSelection) {
            this.setDestination(null);
            this.setOrigin(null);
        }
    },
    mounted() {
        let updateScheduled = false;
        this.stopFinder = this.getStopFinder;
        const adjustPacContainers = () => {
            const pacs = document.getElementsByClassName('pac-container');
            for (let i = 0; i < pacs.length; i++) {
                const pac = pacs[i];

                const isVisible = pac.style.display !== 'none';

                if (isVisible) {
                    if (this.showLocationButton && pac.style.getPropertyValue('top') !== '280px')
                        pac.style.setProperty('top', '280px', 'important');
                    else if (!this.showLocationButton && pac.style.getPropertyValue('top') !== '230px')
                        pac.style.setProperty('top', '230px', 'important');
                }
            }
            updateScheduled = false;
        }

        // Start observing mutations on the body (Google injects .pac-container into <body>)
        const observer = new MutationObserver(() => {
            if (!updateScheduled) {
                updateScheduled = true;
                requestAnimationFrame(adjustPacContainers);
            }
        });
        observer.observe(document.body, {
            childList: true,      // Watch for added/removed children
            subtree: true,        // Watch entire DOM subtree
            attributes: true,     // Watch style/display changes
            attributeFilter: ['style', 'class'] // Only care about changes to style/class
        });
        this.pacObserver = observer;


        if (this.getSentBacktoSelection) {

            const originBox = document.getElementById('originBox');
            originBox.value = this.getOrigin.searchedName; 
            if (this.getOrigin.error) {
                originBox.style.border = '2px solid red';
                this.setOrigin(null);
            } else {
                originBox.style.border = '#eee'; 
            }
            
            const destinationBox = document.getElementById('destinationBox');
            destinationBox.value = this.getDestination.searchedName; 
            if (this.getDestination.error) {
                destinationBox.style.border = '2px solid red'; 
                this.setDestination(null);
            } else {
                destinationBox.style.border = '#eee'; 
            }

            this.setSentBacktoSelection(false);
        }
    },

    watch: {
        '$store.state.destination.position': function () {
            if (this.getDestination !== null && this.getOrigin !== null && !this.getSentBacktoSelection) {
                this.$router.push('/confirm');
            }
        },

        '$store.state.origin.position': function () {
            if (this.getDestination !== null && this.getOrigin !== null && !this.getSentBacktoSelection) {
                this.$router.push('/confirm');
            }
        },
    },
    beforeDestroy() {
        if (this.pacobserver) {
            this.pacobserver.disconnect();
            this.pacObserver = null;
        }
    },
}
</script>

<style scoped>
    .my-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 5px;
    }

    .address-box {
        width: calc(100% - 30px);
        height: 50px;
        border-radius: 8px;
        font-size: 100%;
        margin-bottom: 10px;
        background-color: #eee;
        border: #eee;
        padding-left: 10px;
        padding-right: 10px;
        font-family:'Roboto';
    }

    .locationButton {
        position: relative;
        border: 10px;
        color: white;
        padding: 15px 32px;
        text-align: center;
        text-decoration: none;
        font-size: 16px;
        cursor: pointer;
        margin: 5px;
        width: 100%;
        border-radius: 10px;
        background-color: #00C679;
    }

    .v1_6 {
        width: 100%;
        height: 100px;
        background: rgba(255,255,255,1);
        position: relative;
        top: 0px;
        left: 0px;
        overflow: hidden;
        display: flex;
        align-items: center; 
        justify-content: center;
    }
    .v1_5 {
        width: 50%;
        color: rgba(0,0,0,1);
        font-size: 24px;
        text-align: center;
    }
    .v1_4 {
        position: absolute;
        display: flex; 
        align-items: center;
        justify-content: center;
        cursor: pointer;
        left: 10px;
    }

    .pac-item{
        width: calc(100% - 20px);
    }

</style>

<style>
    .pac-item {
        height: 50px !important;
    }
</style>