<template>
    <div v-if="isReady && !endingSession" class="my-container">
        <img v-if="getLang === 'en'" @click="changeLang" class="languageButton" src="../assets/estonia.png" />
        <img v-if="getLang === 'est'" @click="changeLang" class="languageButton" src="../assets/united-kingdom.png" />
        <button @click="endSession" class="end-session-button">
            <p> {{ $store.state.currentLang.endSession }} </p>
        </button>
        <GmapMap :map-type-id='mapType' :center="center" :zoom='zoom' :style= "{ width: width + '%', height: height + 'px' }" :options='mapOptions'>
            <GmapMarker :position="getUserLocation.position" :icon="{ url: require('../assets/bluecircle.png')}" />
            <GmapMarker :position="getCarLocation" :icon="{ url: require('../assets/carmarker.png')}"/>
        </GmapMap>
        <router-link to="/destinationSearch" class="address-box" :placeholder=$store.state.currentLang.whereTo tag="input"></router-link>
    </div>
    <div v-else class="my-container">
        <p v-if="!endingSession" class="map-message"> {{ $store.state.currentLang.loadingMap }} </p>
        <p v-else class="map-message"> {{ $store.state.currentLang.endingMessage }} </p>
        <div class="loader"></div> 
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex';

export default {
    name: 'Map',
    
    data() {
        return {
            center: { lat: 58.379717565420194, lng: 26.72276002855532 },
            zoom: 15,
            mapType: 'roadmap',
            mapOptions: {zoomControl: false, fullscreenControl: false, streetViewControl: false, mapTypeControl: false, preserveViewport: true },
            markers: [],
            height: 0,
            width: 100,
            endingSession: false,
        }
    },

    computed: {
        ...mapGetters(['getMarkers', 'getUserLocation', 'getGeolocationWatcher','getCarLocation', 'getLang', 'getCurrentLang', 'getMqttBridge']),
        isReady() {
            return this.getUserLocation && this.getCarLocation;
        },
    },

    methods: {
        ...mapActions(['addMarker', 'setLang', 'setCurrentLang', 'setGeolocationWatcher', 'setIsSessionValid', 'setSessionID']),

        changeLang: function() {
            if (this.getLang === 'est') {
                this.setLang('en');
                this.setCurrentLang(this.$store.state.locale.en);
            }
            else {
                this.setLang('est');
                this.setCurrentLang(this.$store.state.locale.est);
            }
        },

        endSession: function() {
            this.endingSession = true;
            navigator.geolocation.clearWatch(this.getGeolocationWatcher);
            this.getMqttBridge.disconnect();
            this.$store.dispatch('resetStateToDefault');
            this.setGeolocationWatcher(undefined);
            this.setIsSessionValid(false);

            // Remove query parameters before the hash from the URL
            const newUrl = `${window.location.origin}${window.location.pathname}${window.location.hash}`;
            window.history.replaceState({}, '', newUrl);

            this.$router.push('/');
        },
    },

    watch: {
        isReady: function(newVal) {
            if (!newVal) {
                this.height = window.innerHeight; 
            }
            else {
                this.height = window.innerHeight - 70;
                this.center = this.getUserLocation.position;
            }
        }
    },

    created: function() {
        this.height = window.innerHeight - 70;
        if (this.getUserLocation) {
            this.center = this.getUserLocation.position;
        }
    },
}
</script>

