    ::placeholder {
        color: black;
    }

    .my-container {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .address-box {
        width: calc(100% - 20px);
        height: 50px;
        border-radius: 8px;
        font-size: 100%;
        margin-top: 10px;
        padding-left: 10px;
        padding-right: 10px;
        background-color: #eee;
        border: #eee
    }

    .languageButton {
        position: absolute;
        top: 10px;
        right: 10px;
        z-index: 1;
        height: 25px;
        width: 25px;
        cursor: pointer;
    }

    .end-session-button {
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        font-size: 16px;

        position: absolute;
        top: 10px;
        left: 15px;
        z-index: 1;
        height: 40px;
        width: 140px;
        cursor: pointer;
        background-color: #007bff;
        color: white;
        border: none;
        border-radius: 5px;
    }
    .end-session-button:hover {
        background-color: #0056b3;
    }

    .map-message {
        margin-top: 20px;
        font-size: 20px;
    }
    .loader {
        border: 16px solid #adadad; /* Light grey */
        border-top: 16px solid #2987c5; /* Blue */
        border-radius: 50%;
        width: 60px;
        height: 60px;
        animation: spin 2s linear infinite;
        margin-top: 10px;
    }
    @keyframes spin {
        0% { transform: rotate(0deg); }
        100% { transform: rotate(360deg); }
    }

</style>