<template>
    <div id="app">
        <router-view></router-view>
    </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import { StopFinder } from './utils/stopFinder.js';

export default {
    name: "App",
    components: {
    },

    data() {
        return {
            polling: null,
            ip: "",
            location: null,
            mqttBridge: null,
        }
    },

    methods: {
        ...mapActions(['setUserLocation', 'setIP', 'setGeolocationWatcher', 'setCarLocation', 'setMqttBridge', 'setStopFinder']),
        ...mapGetters(['getShowLocationButton', 'getGeolocationWatcher', 'getMqttBridge', 'getSessionId']),

        geolocate: function() {
            const geolocationWatcher = navigator.geolocation.watchPosition(
                position => {
                    this.location = {
                        lat: position.coords.latitude,
                        lng: position.coords.longitude,
                    };
                    this.setUserLocation({ position: this.location });
                }
            );
            this.setGeolocationWatcher(geolocationWatcher);
        },
        startAppLogic() {
            this.ip = window.location.host.slice(0, -5);
            this.setIP(this.ip);

            this.setStopFinder(new StopFinder());
            this.mqttBridge = this.getMqttBridge();

            this.carLocation();
        },
        carLocation: function() {
            this.polling = setInterval(() => {
                this.setCarLocation(this.mqttBridge.getPose());
            } , 1000);
        },
    },
    watch: {
        '$store.state.isSessionValid': function(newVal) {
            if(this.polling) {
                    clearInterval(this.polling);
                    this.polling = null;
            }
            if (newVal) {
                this.startAppLogic();
            }
        },
        '$store.state.geolocationWatcher': function(newVal) {
            if (!newVal) {
                this.geolocate();
            }
        },
    },
    mounted: function() {
        this.geolocate();
    }
};
</script>

<style>
    * {
        font-family: 'Roboto', sans-serif; 
        font-weight: 500 !important;
    }

    body {
        margin-top: 0px; 
        margin-bottom: 0px; 
